// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Grade } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetGrade() {
  const [data, setData] = useState<Response<Grade[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.grade.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createGrade(grade: any) {
  const { data } = await sdk.course.offline.grade.create(grade);

  return data;
}

// ----------------------------------------------------------------------

export async function updateGrade(grade: any, id: string) {
  const { data } = await sdk.course.offline.grade.update(id, grade);

  return data;
}
