// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, School } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetSchool() {
  const [data, setData] = useState<Response<School[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.school.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createSchool(school: any) {
  const { data } = await sdk.course.offline.school.create(school);

  return data;
}

// ----------------------------------------------------------------------

export async function updateSchool(school: any, id: string) {
  const { data } = await sdk.course.offline.school.update(id, school);

  return data;
}
