// react
import { useEffect, useState } from 'react';
// MUi
import { FormControl, InputLabel, MenuItem, Select, Stack } from '@mui/material';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
// api
import useGetClass from 'src/api/controllers/class';
import useGetGrade from 'src/api/controllers/grade';
import useGetSchool from 'src/api/controllers/school';
import useGetStudent from 'src/api/controllers/student';
// route
import { paths } from 'src/routes/paths';
// utils
import { fDate, fDateTime } from 'src/utils/format-time';
//
import { TableHeader, TableRows } from 'src/sections/student/student-table-item';
// table
import TableData from '../../table/table-data';
// context
import { useTableContext } from '../../table/context';

// ----------------------------------------------------------------------

export default function AllStudentView() {
  const {
    _table_data,
    //
    onSetTableData,
    onSetTableHeader,
    onSetTableRows,
    onSetTableRowCollapse,
    onSetImportTableData,
    onSetExportTableData,
    onSetConfigTable,
  } = useTableContext();

  const { data: schools } = useGetSchool();

  const { data: classes } = useGetClass();

  const { data: grades } = useGetGrade();

  const { data: students } = useGetStudent();

  const [schoolId, setSchoolId] = useState('');

  const [gradeId, setGradeId] = useState();

  const [classId, setClassId] = useState('');

  const filterGrade = grades?.filter((grade: any) => grade?.school?.id === schoolId);

  const filterClasses = classes?.filter((classroom: any) => classroom?.grade?.id === gradeId);

  const filterStudent = students?.filter((student: any) => student?.class?.id === classId);

  useEffect(() => {
    onSetTableData(
      filterStudent?.map((student: any) => ({
        ...student,
        type: student?.class?.name,
      }))
    );

    onSetImportTableData({
      sample_data: [],
      option_field: null,
      option_values: [],
    });

    onSetConfigTable({
      table_name: 'student',
      add_new: false,
      //
      filter_by_date: true,
      filter_by_type: false,
      filter_by_search: true,
      //
      import_data: false,
      export_data: true,
      //
      table_selected: true,
      allocation_student: true,
      delete_all: false,
      collapse_item: false,
      //
      edit_row: false,
      delete_row: false,
    });

    onSetTableHeader(TableHeader);
    onSetTableRows(TableRows);
    onSetTableRowCollapse(null);
  }, [students, classId]);

  useEffect(() => {
    onSetExportTableData(
      _table_data?.map((course: any) => ({
        'Mã học sinh': course?.studentId,
        'Tên học sinh': course?.name,
        'Ngày sinh': fDate(course?.dob),
        'Mô tả học sinh': course?.description,
        'Ngày tạo': fDateTime(course?.createdAt),
        'Ngày cập nhật': fDateTime(course?.updatedAt),
      }))
    );
  }, [_table_data]);
  return (
    <>
      <CustomBreadcrumbs
        heading="Phân bổ học sinh"
        links={[
          { name: 'Học sinh' },
          {
            name: 'Danh sách',
            href: paths.home.classroom.root,
          },
          { name: 'Phân bổ học sinh' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Stack direction="row" sx={{ px: 3, pb: 3 }} spacing={2}>
        <FormControl>
          <InputLabel>Lựa chọn trường học...</InputLabel>
          <Select
            label="Lựa chọn trường học..."
            value={schoolId}
            onChange={(event: any) => setSchoolId(event.target.value)}
            sx={{ width: 300 }}
          >
            {schools?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {option?.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <InputLabel>Lựa chọn khối lớp...</InputLabel>
          <Select
            disabled={!schoolId}
            label="Lựa chọn khối lớp..."
            value={gradeId}
            onChange={(event: any) => setGradeId(event.target.value)}
            sx={{ width: 300 }}
          >
            {filterGrade?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {`Khối lớp ${option?.label}`}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <InputLabel>Lựa chọn lớp học...</InputLabel>
          <Select
            disabled={!schoolId}
            label="Lựa chọn lớp học..."
            value={classId}
            onChange={(event: any) => setClassId(event.target.value)}
            sx={{ width: 300 }}
          >
            {filterClasses?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {`Lớp ${option?.grade?.label}${option?.name}`}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Stack>

      <TableData grades={grades} />
    </>
  );
}
