// mui
import TableCell from '@mui/material/TableCell';
// components
import { CustomInfoBar } from 'src/components/custom-info-bar';
// sdk
import { Student } from '@iit/sdk/dist/types';
// utils
import { fDate } from 'src/utils/format-time';

// ----------------------------------------------------------------------

export const TableHeader = [
  { id: 'name', label: 'Tên học sinh', flex: 1 },
  { id: 'studentId', label: 'Ngày sinh', flex: 1, align: 'center' },
  { id: 'class', label: 'Lớp đang học', flex: 1, align: 'center' },
  { id: 'createdAt', label: 'Ngày tạo', flex: 1, align: 'center' },
  { id: 'updatedAt', label: 'Ngày cập nhật', flex: 1, align: 'center' },
  { id: 'action', label: 'Hành động', flex: 1, align: 'right' },
];

export const TableRows = (row: Student) => {
  return (
    <>
      <TableCell>
        <CustomInfoBar primary={row?.name} secondary={row?.studentId} />
      </TableCell>

      <TableCell align="center">
        <CustomInfoBar
          primary={fDate(row?.dob, 'dd/MM/yyyy')}
          secondary={`Giới tính : ${row?.gender ? 'Nam' : 'Nữ'}`}
        />
      </TableCell>

      <TableCell align="center">
        <CustomInfoBar
          primary={`Lớp ${row?.class?.grade?.label}${row?.class?.name}`}
          secondary={`Niên khoá ${fDate(row?.class?.year?.start, 'yyyy')} - ${fDate(
            row?.class?.year?.end,
            'yyyy'
          )}`}
        />
      </TableCell>
    </>
  );
};
