// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Student } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetStudent() {
  const [data, setData] = useState<Response<Student[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.student.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createStudent(student: any) {
  const { data } = await sdk.course.offline.student.create(student);

  return data;
}

// ----------------------------------------------------------------------

export async function updateStudent(student: any, id: string) {
  const { data } = await sdk.course.offline.student.update(id, student);

  return data;
}
